class SalesComponent {
    constructor() {
        this.chartType = 'line';
        this.barChartOptions = {
            scaleShowVerticalLines: false,
            responsive: true
        };
        this.mbarChartLabels = ['2012', '2013', '2014', '2015', '2016', '2017', '2018'];
        this.barChartType = 'bar';
        this.barChartLegend = true;
        this.barChartColors = [
            {
                backgroundColor: 'rgb(1, 192, 200)',
                borderColor: 'rgba(105,159,177,1)',
                pointBackgroundColor: 'rgba(105,159,177,1)',
                pointBorderColor: '#fafafa',
                pointHoverBackgroundColor: '#fafafa',
                pointHoverBorderColor: 'rgba(105,159,177)'
            },
            {
                backgroundColor: 'rgb(70, 159, 180)',
                borderColor: 'rgba(77,20,96,1)',
                pointBackgroundColor: 'rgba(77,20,96,1)',
                pointBorderColor: '#fff',
                pointHoverBackgroundColor: '#fff',
                pointHoverBorderColor: 'rgba(77,20,96,1)'
            }
        ];
        this.barChartData = [
            { data: [55, 60, 75, 82, 56, 62, 80], label: 'Company A' },
            { data: [58, 55, 60, 79, 66, 57, 90], label: 'Company B' }
        ];
    }
    ngOnInit() {
        this.chartData = [{
                data: [3, 1, 4, 2, 5],
                label: 'Sales',
                fill: true
            }];
        this.chartLabels = ['Jan', 'Feb', 'Mar', 'Apr', 'May'];
        this.chartColors = [{
                backgroundColor: 'rgb(1, 192, 200)',
                borderColor: '105,159,177,1'
            }];
        this.chartOptions = {
            scales: {
                yAxes: [{
                        ticks: {
                            beginAtZero: true,
                            stepSize: 1
                        }
                    }]
            },
            annotation: {
                drawTime: 'beforeDatasetsDraw',
                annotations: [{
                        type: 'box',
                        id: 'a-box-1',
                        yScaleID: 'y-axis-0',
                        yMin: 0,
                        yMax: 1,
                        backgroundColor: '#4cf03b'
                    }, {
                        type: 'box',
                        id: 'a-box-2',
                        yScaleID: 'y-axis-0',
                        yMin: 1,
                        yMax: 2.7,
                        backgroundColor: '#fefe32'
                    }, {
                        type: 'box',
                        id: 'a-box-3',
                        yScaleID: 'y-axis-0',
                        yMin: 2.7,
                        yMax: 5,
                        backgroundColor: '#fe3232'
                    }]
            }
        };
    }
    // events
    chartClicked(e) {
        // console.log(e);
    }
    chartHovered(e) {
        // console.log(e);
    }
    randomize() {
        let data = [
            Math.round(Math.random() * 100),
            Math.round(Math.random() * 100),
            Math.round(Math.random() * 100),
            (Math.random() * 100),
            Math.round(Math.random() * 100),
            (Math.random() * 100),
            Math.round(Math.random() * 100)
        ];
        let clone = JSON.parse(JSON.stringify(this.barChartData));
        clone[0].data = data;
        this.barChartData = clone;
    }
}


